.header {
  display: flex;
  align-items: center;
  width: 100%;
  height: 70px;

  &__wrapper {
    display: flex;
    align-items: center;
  }

  &__logotip {
    display: block;
    flex-shrink: 0;
    width: 216px;
    height: 22px;
    margin-right: 60px;
    background-image: url("../images/logo.svg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
  }

  &__burger-menu {
    display: none;
  }

  &__navitation {
    display: flex;
    flex-grow: 1;
    align-items: center;
    width: max-content;
    margin-right: 45px;
  }

  &__item-navigation {
    font-family: "Cera Pro", sans-serif;
    font-size: 13px;
    font-weight: bold;
    line-height: 150%;
    color: #53556c;
    text-transform: uppercase;

    &:first-child {
      margin-right: 20px;
    }
  }

  &__callback-btn {
    margin-left: 48px;
  }

  &__login-btns {
    display: flex;
    margin-right: 43px;
    margin-left: auto;
  }

  &__login-btn {
    display: flex;
    align-items: center;
    height: 24px;
    font-family: "Cera Pro", sans-serif;
    font-size: 13px;
    font-weight: bold;
    line-height: 24px;
    color: $dark-gray3;
    text-transform: uppercase;
    background-color: transparent;

    &_personal-account {
      padding-left: 34px;
      background-image: url("../images/user.svg");
      background-repeat: no-repeat;
      background-position: left center;
      background-size: 24px;
    }

    &:last-child {
      position: relative;
      margin-left: 20px;

      &::before {
        position: absolute;
        top: 6px;
        left: -10px;
        display: block;
        width: 2px;
        height: 11px;
        content: "";
        background-color: #abacb9;
      }
    }
  }

  &__right-buttons {
    position: relative;
    display: flex;
    align-items: center;
    padding-left: 44px;
  }

  &__right-btn {
    position: relative;
    display: block;
    width: 32px;
    height: 24px;
    background-color: transparent;
    background-repeat: no-repeat;
    background-position: center;
    background-size: 24px;

    &_favorite {
      margin-right: 8px;
      background-image: url("../images/heart.svg");
    }

    &_cart {
      background-image: url("../images/cart.svg");
    }
  }

  &__right-btn-counter {
    position: absolute;
    right: -3px;
    bottom: -7px;
    display: inline-block;
    width: 20px;
    height: 20px;
    font-family: "Cera Pro", sans-serif;
    font-size: 12px;
    font-style: normal;
    font-weight: bold;
    line-height: 20px;
    color: $white;
    text-transform: uppercase;
    background-color: $primary-accent;
    border-radius: 5px;
  }
}

@media screen and (max-width: 1200px) {
  .header {
    position: relative;
    height: auto;

    &::after {
      position: absolute;
      bottom: 0;
      left: 0;
      z-index: -1;
      display: block;
      width: 100%;
      height: 44px;
      content: "";
      background-color: $light-gray5;
    }

    &__wrapper {
      display: grid;
      grid-template-rows: 58px 44px;
      grid-template-columns: repeat(2, auto);
    }

    &__logotip {
      grid-area: 1/1/2/3;
      margin: 0 auto;
    }

    &__burger-menu {
      display: block;
      grid-area: 2/1/3/2;
      width: max-content;
      padding-left: 38px;
      font-family: "Cera Pro", sans-serif;
      font-size: 14px;
      font-weight: 500;
      line-height: 130%;
      color: $dark-gray3;
      text-transform: uppercase;
      background-color: transparent;
      background-image: url("../images/burger.svg");
      background-repeat: no-repeat;
      background-position: left center;
      background-size: 23px 14px;
      transition: background-image 0.3s;

      &_open {
        background-image: url("../images/cross.svg");
      }
    }

    &__navitation {
      position: absolute;
      top: 102px;
      left: 0;
      z-index: 20;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      width: 100%;
      height: 0;
      overflow: hidden;
      background-color: $light-gray5;
      transition: height 0.4s;

      &_open {
        height: calc(100vh - 102px);
        transition: height 0.4s;
      }
    }

    &__item-navigation {
      margin-bottom: 25px;

      &:not(:last-child) {
        margin-right: 0;
      }
    }

    &__callback-btn {
      margin-bottom: 25px;
      margin-left: 0;
    }

    &__login-btns {
      margin: 0;
    }

    &__right-buttons {
      margin-left: auto;
    }
  }
}
