.modal {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 30;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  overflow-y: auto;
  background-color: rgba(255, 255, 255, 0.5);

  &__form {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    width: 94%;
    max-width: 611px;
    height: max-content;
    padding: 62px 117px;
    margin: auto;
    background: $white;
    box-shadow: 0 -2px 32px rgba(0, 0, 0, 0.25);
  }

  &__title {
    font-family: "Cera Pro", sans-serif;
    font-size: 28px;
    font-weight: 500;
    line-height: 130%;
    color: $dark-gray3;
  }

  &__btn-submit {
    width: 100%;
    margin-top: 20px;
  }

  &__btn-close {
    position: absolute;
    top: 20px;
    right: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 32px;
    height: 32px;
    background-color: transparent;
  }

  &__btn-close-svg {
    display: inline-block;
    width: 15px;
    height: 15px;
    fill: $light-gray2;
    transition: fill 0.4s;
  }

  &__btn-close:hover &__btn-close-svg {
    fill: $error;
  }

  &__footer {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;

    & button {
      background-color: transparent;
    }
  }

  &__forgot-password {
    position: relative;
    display: block;
    margin: 20px auto;
    margin-bottom: 40px;
    background-color: transparent;

    &::after {
      position: absolute;
      bottom: -20px;
      left: -80%;
      display: block;
      width: 260%;
      height: 1px;
      content: "";
      background-color: $light-gray3;
    }
  }

  &__btn-underline {
    display: inline-block;
    margin: 15px auto;
    font-family: "Cera Pro", sans-serif;
    font-size: 14px;
    line-height: 150%;
    text-decoration-line: underline;
    color: $primary;
    background-color: transparent;
  }

  &__small {
    display: block;
    width: 100%;
    margin: 5px 0;
    font-family: "Cera Pro", sans-serif;
    font-size: 10px;
    line-height: 150%;
    color: $light-gray1;
    text-align: left;
  }
}

@media screen and (max-width: 650px) {
  .modal {
    &__form {
      padding: 35px;
    }
  }
}

.final-subscribe__wrapper {
  align-items: center;
  justify-content: center;
}

.final__title,
.final-subscribe__title,
.ask__title {
  font-family: "Cera Pro", sans-serif;
  font-size: 22px;
  font-weight: 500;
  line-height: 28px;
  color: $dark-gray3;
  letter-spacing: -0.02em;
}

.registration-with-inn {
  &__upload-files {
    margin-bottom: 20px;
  }

  &__btn-submit {
    width: 100%;
    margin-bottom: 20px;
  }

  &__input-title {
    margin-top: 20px;
    margin-bottom: 5px;
    margin-left: 20px;
    font-family: "Cera Pro", sans-serif;
    font-size: 11px;
    line-height: 130%;
    color: $dark-gray3;
  }
}
