.bread-crumbs {
  position: relative;
  display: flex;
  align-items: center;
  height: max-content;
  padding: 20px 0;
  overflow: hidden;

  &::after {
    position: absolute;
    top: 0;
    right: 0;
    display: block;
    width: 50px;
    height: 100%;
    content: "";
    background: linear-gradient(90deg, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 1) 59%);
  }

  &__link {
    display: flex;
    flex-shrink: 0;
    font-family: "Cera Pro", sans-serif;
    font-size: 14px;
    line-height: 150%;
    color: $dark-gray2;

    &::after {
      margin: auto 10px;
      content: "-";
    }

    &:last-child {
      color: $light-gray1;

      &::after {
        content: "";
      }
    }
  }
}

