* {
  padding: 0;
  margin: 0;
  border: 0;
}

*,
*::before,
*::after {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

:focus,
:active {
  outline: none;
}

a,
a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

a:focus,
a:active {
  outline: none;
}

nav,
footer,
header,
aside {
  display: block;
}

html,
body {
  width: 100%;
  height: 100%;
  font-size: 100%;
  font-size: 14px;
  line-height: 1;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

input,
button,
textarea {
  font-family: inherit;
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: 400;
}

@font-face {
  font-family: "Cera Pro", sans-serif;
  font-display: swap;
  font-style: normal;
  font-weight: 400;
  src: local("Cera Pro"), url("../fonts/CeraPro-Regular.woff2") format("woff2");
}

@font-face {
  font-family: "Cera Pro", sans-serif;
  font-display: swap;
  font-style: normal;
  font-weight: 500;
  src: local("Cera Pro"), url("../fonts/CeraPro-Medium.woff2") format("woff2");
}

@font-face {
  font-family: "Cera Pro", sans-serif;
  font-display: swap;
  font-style: normal;
  font-weight: 700;
  src: local("Cera Pro"), url("../fonts/CeraPro-Bold.woff2") format("woff2");
}

body {
  font-family: "Cera Pro", serif;
}

.wrapper {
  width: 94%;
  max-width: 1220px;
  margin: 0 auto;
}

.title-section {
  width: max-content;
  font-family: "Cera Pro", sans-serif;
  font-size: 36px;
  font-weight: bold;
  line-height: 130%;
  color: #434a54;
  text-transform: uppercase;
}

.page-title {
  font-family: "Cera Pro", sans-serif;
  font-size: 36px;
  font-weight: bold;
  line-height: 130%;
  color: #231f20;
  text-transform: uppercase;
}

.body-text {
  font-family: "Cera Pro", sans-serif;
  font-size: 14px;
  line-height: 150%;
  color: #393a4a;
}

.secondary-button {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 11px 42px;
  font-family: "Cera Pro", sans-serif;
  font-size: 14px;
  font-weight: 500;
  line-height: 150%;
  color: #53556c;
  background-color: transparent;
  border: 1px solid #53556c;
  border-radius: 7px;
  transition: background-color 0.3s, color 0.3s;
}

.secondary-button:hover {
  color: #fff;
  background-color: #53556c;
  transition: background-color 0.3s, color 0.3s;
}

.secondary-button:disabled {
  pointer-events: none;
  opacity: 0.4;
}

.secondary-button:disabled:hover {
  color: #53556c;
  background-color: transparent;
}

.secondary-button_small {
  padding: 5px 20px;
  font-size: 11px;
  color: #003729;
  text-transform: uppercase;
  border: 1px solid #003729;
}

.secondary-button_small:hover {
  color: #fff;
  background-color: rgba(0, 55, 41, 0.5);
  transition: background-color 0.3s, color 0.3s;
}

.primary-button {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 12px 42px;
  font-family: "Cera Pro", sans-serif;
  font-size: 14px;
  font-weight: bold;
  line-height: 150%;
  color: #fff;
  background-color: #003729;
  border-radius: 7px;
  transition: background-color 0.3s;
}

.primary-button:hover {
  background-color: rgba(0, 55, 41, 0.8);
  transition: background-color 0.3s;
}

.primary-button:disabled {
  opacity: 0.4;
}

.primary-button:disabled:hover {
  background-color: transparent;
}

.primary-button_small {
  height: 40px;
  padding: 7px 20px;
  font-size: 11px;
  line-height: 11px;
  color: #fff;
  background-color: #003729;
}

.primary-button_small:hover {
  background-color: rgba(0, 55, 41, 0.8);
  transition: background-color 0.3s;
}

.input {
  position: relative;
  display: block;
  width: 100%;
  font-family: "Cera Pro", sans-serif;
}

.input__title {
  margin-bottom: 4px;
  margin-left: 22px;
  font-family: "Cera Pro", sans-serif;
  font-size: 12px;
  line-height: 18px;
  color: #393a4a;
  opacity: 0;
  transition: opacity 0.3s;
}

.input_focus .input__title {
  opacity: 1;
  transition: opacity 0.3s;
}

.input__item {
  display: flex;
  align-items: center;
  width: 100%;
  height: 40px;
  padding: 0 20px;
  padding-right: 45px;
  font-family: "Cera Pro", sans-serif;
  font-size: 14px;
  line-height: 150%;
  color: #393a4a;
  background-color: #fff;
  border: 1px solid #abacb9;
  border-radius: 5px;
}

.input_valide::after {
  position: absolute;
  top: 18px;
  right: 20px;
  display: block;
  width: 20px;
  height: 40px;
  content: "";
  background-image: url("../images/valide-form.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
}

.input_required::after {
  position: absolute;
  top: 18px;
  right: 20px;
  display: block;
  width: 20px;
  height: 40px;
  content: "";
  background-image: url("../images/required.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: 8px;
}

.input__error {
  margin-top: 4px;
  margin-left: 22px;
  font-family: "Cera Pro", sans-serif;
  font-size: 12px;
  line-height: 18px;
  color: #d00000;
  opacity: 0;
  transition: opacity 0.3s;
}

.input_error .input__error {
  opacity: 1;
  transition: opacity 0.3s;
}

.password {
  position: relative;
  display: block;
  width: 100%;
  font-family: "Cera Pro", sans-serif;
}

.password__title {
  margin-bottom: 4px;
  margin-left: 22px;
  font-family: "Cera Pro", sans-serif;
  font-size: 12px;
  line-height: 18px;
  color: #393a4a;
  opacity: 0;
  transition: opacity 0.3s;
}

.password_focus .password__title {
  opacity: 1;
  transition: opacity 0.3s;
}

.password__item {
  display: flex;
  align-items: center;
  width: 100%;
  height: 40px;
  padding: 0 20px;
  padding-right: 45px;
  font-family: "Cera Pro", sans-serif;
  font-size: 14px;
  line-height: 150%;
  color: #393a4a;
  background-color: #fff;
  border: 1px solid #abacb9;
  border-radius: 5px;
}

.password__toggle-show {
  position: absolute;
  top: 18px;
  right: 20px;
  display: block;
  width: 20px;
  height: 40px;
  background-color: transparent;
  background-image: url("../images/Hide.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: 15px;
  transition: background-image 0.4s;
}

.password_show .password__toggle-show {
  background-image: url("../images/Show.svg");
}

.password__error {
  margin-top: 4px;
  margin-left: 22px;
  font-family: "Cera Pro", sans-serif;
  font-size: 12px;
  line-height: 18px;
  color: #d00000;
  opacity: 0;
  transition: opacity 0.3s;
}

.password_error .password__error {
  opacity: 1;
  transition: opacity 0.3s;
}

.select {
  position: relative;
  display: block;
  width: 100%;
  height: 40px;
  margin-top: 20px;
  font-family: "Cera Pro", sans-serif;
  font-size: 14px;
  line-height: 40%;
  color: #393a4a;
  cursor: pointer;
  border: 1px solid #abacb9;
  border-radius: 5px;
}

.select::after {
  position: absolute;
  top: 0;
  right: 20px;
  display: block;
  width: 10px;
  height: 40px;
  pointer-events: none;
  content: "";
  background-image: url("../images/arrow-down.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
}

.select_open::after {
  transform: rotate3d(1, 0, 0, 180deg);
}

.select__label {
  position: absolute;
  top: -20px;
  left: 22px;
  display: none;
  font-family: "Cera Pro", sans-serif;
  font-size: 12px;
  line-height: 18px;
  color: #53556c;
}

.select_focus .select__label {
  display: inline-block;
}

.select__item {
  width: 100%;
  height: 100%;
  padding-right: 40px;
  padding-left: 20px;
  background: #fff;
  border-radius: 5px;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

.checkbox {
  position: relative;
  display: flex;
  flex-direction: row-reverse;
  align-items: center;
  width: max-content;
  max-width: 100%;
  padding-left: 24px;
  font-family: "Cera Pro", sans-serif;
  font-size: 14px;
  line-height: 150%;
  color: #53556c;
  cursor: pointer;
}

.checkbox::before {
  position: absolute;
  top: 4px;
  left: 0;
  display: block;
  width: 13px;
  height: 13px;
  content: "";
  border: 1px solid rgba(0, 55, 41, 0.8);
}

.checkbox_checked::after {
  position: absolute;
  top: 4px;
  left: 0;
  display: block;
  width: 13px;
  height: 13px;
  content: "";
  background-image: url("../images/valide-form.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: 9px;
}

.checkbox__input {
  position: absolute;
  top: 0;
  left: 0;
  width: 1px;
  height: 1px;
  opacity: 0;
}

.radio-payment {
  position: relative;
  display: flex;
  flex-direction: column-reverse;
  width: 100%;
  height: max-content;
  padding: 30px;
  margin-bottom: 20px;
  font-family: "Cera Pro", sans-serif;
  font-size: 13px;
  font-weight: bold;
  line-height: 150%;
  color: #53556c;
  text-transform: uppercase;
  cursor: pointer;
  background: #f7f7f8;
  border: 1px solid #abacb9;
  border-radius: 5px;
}

.radio-payment::after {
  position: absolute;
  top: 50px;
  right: 32px;
  width: 18px;
  height: 18px;
  content: "";
  border: 2px solid rgba(0, 55, 41, 0.8);
  border-radius: 5px;
}

.radio-payment_checked::after {
  background-image: url("../images/valide-form.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: 10px;
}

.radio-payment__svg {
  display: inline-block;
  width: 24px;
  height: 24px;
  margin-bottom: 25px;
  fill: #fff;
  stroke: #53556c;
}

.radio-payment__input {
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
}

.upload-files {
  display: block;
  width: 100%;
  height: 162px;
  background-color: #fff;
  background-image: url("../images/upload-file.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: 38px;
  border: 1px solid #abacb9;
  border-radius: 5px;
}

.upload-files__input {
  width: 100%;
  height: 100%;
  padding: 20px;
  border-radius: 5px;
}

.upload-files__input_hide {
  opacity: 0;
}

.primary-tabs {
  position: relative;
  display: flex;
  align-items: center;
  width: max-content;
  max-width: 100%;
  height: auto;
  min-height: 36px;
  padding: 5px 24px;
  margin-bottom: 7px;
  font-family: "Cera Pro", sans-serif;
  font-size: 14px;
  line-height: 150%;
  color: #53556c;
  cursor: pointer;
  background-color: transparent;
  border: 1px solid #53556c;
  border-radius: 5px;
  box-shadow: 0 2px 9px rgba(0, 0, 0, 0.15);
  transition: color 0.4s;
}

.primary-tabs:hover {
  color: #fff;
  transition: color 0.4s;
}

.primary-tabs__input {
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  width: 100%;
  height: 100%;
  cursor: pointer;
  background-color: transparent;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  border-radius: 5px;
  transition: background-color 0.4s;
}

.primary-tabs__input:checked {
  background-color: #dddee3;
  transition: background-color 0.4s;
}

.primary-tabs__input:hover {
  color: #fff;
  background-color: #bcbdc7;
  transition: background-color 0.4s;
}

.add-cart {
  display: flex;
  align-items: center;
  justify-content: center;
  width: max-content;
  padding: 12px 35px;
  font-family: "Cera Pro", sans-serif;
  font-size: 12px;
  font-weight: bold;
  line-height: 150%;
  color: #fff;
  text-transform: uppercase;
  background-color: #003729;
  border-radius: 5px;
  transition: background-color 0.4s;
}

.add-cart__svg {
  display: inline-block;
  width: 24px;
  height: 24px;
  margin-left: 10px;
  fill: transparent;
  stroke: #fff;
}

.add-cart:hover {
  background-color: rgba(0, 55, 41, 0.8);
}

.add-cart_disable {
  pointer-events: none;
  opacity: 0.4;
}

.tabs-toggle {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 65px;
  margin-bottom: 35px;
  overflow: hidden;
  border: 1px solid #abacb9;
  border-radius: 10px;
}

.tabs-toggle__btn {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  padding: 14px 0;
  font-family: "Cera Pro", sans-serif;
  font-size: 13px;
  font-weight: bold;
  line-height: 150%;
  color: #53556c;
  text-transform: uppercase;
  background-color: transparent;
  transition: color 0.4s, background-color 0.4s;
}

.tabs-toggle__btn_selected {
  color: #fff;
  background-color: rgba(0, 55, 41, 0.8);
  border-radius: 10px;
}

input[type="number"]::-webkit-outer-spin-button,
input[type="number"]::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type='number'] {
  -moz-appearance: textfield;
}

.header {
  display: flex;
  align-items: center;
  width: 100%;
  height: 70px;
}

.header__wrapper {
  display: flex;
  align-items: center;
}

.header__logotip {
  display: block;
  flex-shrink: 0;
  width: 216px;
  height: 22px;
  margin-right: 60px;
  background-image: url("../images/logo.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
}

.header__burger-menu {
  display: none;
}

.header__navitation {
  display: flex;
  flex-grow: 1;
  align-items: center;
  width: max-content;
  margin-right: 45px;
}

.header__item-navigation {
  font-family: "Cera Pro", sans-serif;
  font-size: 13px;
  font-weight: bold;
  line-height: 150%;
  color: #53556c;
  text-transform: uppercase;
}

.header__item-navigation:first-child {
  margin-right: 20px;
}

.header__callback-btn {
  margin-left: 48px;
}

.header__login-btns {
  display: flex;
  margin-right: 43px;
  margin-left: auto;
}

.header__login-btn {
  display: flex;
  align-items: center;
  height: 24px;
  font-family: "Cera Pro", sans-serif;
  font-size: 13px;
  font-weight: bold;
  line-height: 24px;
  color: #53556c;
  text-transform: uppercase;
  background-color: transparent;
}

.header__login-btn_personal-account {
  padding-left: 34px;
  background-image: url("../images/user.svg");
  background-repeat: no-repeat;
  background-position: left center;
  background-size: 24px;
}

.header__login-btn:last-child {
  position: relative;
  margin-left: 20px;
}

.header__login-btn:last-child::before {
  position: absolute;
  top: 6px;
  left: -10px;
  display: block;
  width: 2px;
  height: 11px;
  content: "";
  background-color: #abacb9;
}

.header__right-buttons {
  position: relative;
  display: flex;
  align-items: center;
  padding-left: 44px;
}

.header__right-btn {
  position: relative;
  display: block;
  width: 32px;
  height: 24px;
  background-color: transparent;
  background-repeat: no-repeat;
  background-position: center;
  background-size: 24px;
}

.header__right-btn_favorite {
  margin-right: 8px;
  background-image: url("../images/heart.svg");
}

.header__right-btn_cart {
  background-image: url("../images/cart.svg");
}

.header__right-btn-counter {
  position: absolute;
  right: -3px;
  bottom: -7px;
  display: inline-block;
  width: 20px;
  height: 20px;
  font-family: "Cera Pro", sans-serif;
  font-size: 12px;
  font-style: normal;
  font-weight: bold;
  line-height: 20px;
  color: #fff;
  text-transform: uppercase;
  background-color: #003729;
  border-radius: 5px;
}

@media screen and (max-width: 1200px) {
  .header {
    position: relative;
    height: auto;
  }
  .header::after {
    position: absolute;
    bottom: 0;
    left: 0;
    z-index: -1;
    display: block;
    width: 100%;
    height: 44px;
    content: "";
    background-color: #f7f7f8;
  }
  .header__wrapper {
    display: grid;
    grid-template-rows: 58px 44px;
    grid-template-columns: repeat(2, auto);
  }
  .header__logotip {
    grid-area: 1/1/2/3;
    margin: 0 auto;
  }
  .header__burger-menu {
    display: block;
    grid-area: 2/1/3/2;
    width: max-content;
    padding-left: 38px;
    font-family: "Cera Pro", sans-serif;
    font-size: 14px;
    font-weight: 500;
    line-height: 130%;
    color: #53556c;
    text-transform: uppercase;
    background-color: transparent;
    background-image: url("../images/burger.svg");
    background-repeat: no-repeat;
    background-position: left center;
    background-size: 23px 14px;
    transition: background-image 0.3s;
  }
  .header__burger-menu_open {
    background-image: url("../images/cross.svg");
  }
  .header__navitation {
    position: absolute;
    top: 102px;
    left: 0;
    z-index: 20;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 0;
    overflow: hidden;
    background-color: #f7f7f8;
    transition: height 0.4s;
  }
  .header__navitation_open {
    height: calc(100vh - 102px);
    transition: height 0.4s;
  }
  .header__item-navigation {
    margin-bottom: 25px;
  }
  .header__item-navigation:not(:last-child) {
    margin-right: 0;
  }
  .header__callback-btn {
    margin-bottom: 25px;
    margin-left: 0;
  }
  .header__login-btns {
    margin: 0;
  }
  .header__right-buttons {
    margin-left: auto;
  }
}

.search {
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  width: max-content;
  height: 100%;
}

.search__input {
  position: absolute;
  top: 0;
  right: 0;
  width: 0;
  height: 0;
}

.search__input_open {
  width: 250px;
  height: 28px;
  padding: 5px 10px;
  padding-right: 30px;
  border: 1px solid #dddee3;
  border-radius: 5px;
}

.search__btn {
  position: relative;
  z-index: 10;
  display: block;
  width: 32px;
  height: 24px;
  background-color: transparent;
  background-image: url("../images/search.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: 24px;
}

.footer {
  width: 100%;
  margin-top: 48px;
  background: #f7f7f8;
}

.footer__wrapper {
  padding-top: 35px;
}

.footer__logo {
  display: block;
  width: 216px;
  height: 22px;
  margin-bottom: 45px;
  background-image: url("../images/logo.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
}

.footer__items {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.footer__item {
  display: flex;
  flex-direction: column;
  flex-shrink: 0;
  width: max-content;
  min-width: 160px;
  height: auto;
}

.footer__item-title {
  max-width: 310px;
  margin-bottom: 18px;
  font-family: "Cera Pro", sans-serif;
  font-size: 13px;
  font-weight: bold;
  line-height: 150%;
  color: #393a4a;
  text-transform: uppercase;
}

.footer__link {
  max-width: 160px;
  font-family: "Cera Pro", sans-serif;
  font-size: 14px;
  line-height: 18px;
  color: #abacb9;
}

.footer__link:not(:last-child) {
  margin-bottom: 15px;
}

.footer__subscribe {
  display: flex;
  align-items: center;
  margin-bottom: 5px;
}

.footer__input {
  width: 258px;
  margin-right: 10px;
}

.footer__media-links {
  display: flex;
  margin-bottom: 25px;
}

.footer__media-links_mobile {
  display: none;
}

.footer__item-media {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 26px;
