  margin-bottom: 33px;
}

.any-questions__form {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  column-gap: 12px;
  width: 100%;
}

.any-questions__input {
  width: 100%;
}

.any-questions__btn {
  display: flex;
  width: 100%;
  max-width: 346px;
  height: max-content;
  padding: 12px 24px;
  margin-top: 16px;
}

@media screen and (max-width: 1000px) {
  .any-questions {
    padding: 40px 0;
  }
  .any-questions__wrapper {
    padding: 40px;
  }
  .any-questions__form {
    display: flex;
    flex-wrap: wrap;
    column-gap: 0;
    align-items: center;
    width: 100%;
  }
  .any-questions__input {
    max-width: 346px;
    margin-right: 12px;
  }
}

@media screen and (max-width: 850px) {
  .any-questions__wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .any-questions__paragraph {
    text-align: center;
  }
  .any-questions__form {
    flex-direction: column;
    align-items: center;
  }
  .any-questions__input {
    margin-right: 0;
  }
  .any-questions__btn {
    margin-top: 15px;
  }
}

.disabled {
  display: none;
}

.stop-scroll {
  overflow: hidden;
}

use {
  width: 100%;
  height: 100%;
}
