.about-laboratory {
  padding: 60px 0;

  &__wrapper {
    position: relative;
    padding-left: 43px;

    &::before {
      position: absolute;
      top: 0;
      left: 0;
      display: block;
      width: 8px;
      height: 100%;
      content: "";
      background-color: #485cc7;
    }
  }

  &__text {
    font-family: "Cera Pro", sans-serif;
    font-size: 14px;
    line-height: 150%;
  }
}
