.footer {
  width: 100%;
  margin-top: 48px;
  background: $light-gray5;

  &__wrapper {
    padding-top: 35px;
  }

  &__logo {
    display: block;
    width: 216px;
    height: 22px;
    margin-bottom: 45px;
    background-image: url("../images/logo.svg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
  }

  &__items {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }

  &__item {
    display: flex;
    flex-direction: column;
    flex-shrink: 0;
    width: max-content;
    min-width: 160px;
    height: auto;

    // &:not(:last-child) {
    //   margin-right: 50px;
    // }
  }

  &__item-title {
    max-width: 310px;
    margin-bottom: 18px;
    font-family: "Cera Pro", sans-serif;
    font-size: 13px;
    font-weight: bold;
    line-height: 150%;
    color: $dark-gray2;
    text-transform: uppercase;
  }

  &__link {
    max-width: 160px;
    font-family: "Cera Pro", sans-serif;
    font-size: 14px;
    line-height: 18px;
    color: $light-gray1;

    &:not(:last-child) {
      margin-bottom: 15px;
    }
  }

  &__subscribe {
    display: flex;
    align-items: center;
    margin-bottom: 5px;
  }

  &__input {
    width: 258px;
    margin-right: 10px;
  }

  // &__subscribe-btn {
  // }

  &__media-links {
    display: flex;
    margin-bottom: 25px;

    &_mobile {
      display: none;
    }
  }

  &__item-media {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 26px;
    height: 26px;

    &:not(:last-child) {
      margin-right: 10px;
    }
  }

  &__item-media-link {
    display: inline-block;
    width: 100%;
    height: 100%;
    background-color: $light-gray1;
    background-repeat: no-repeat;
    background-position: center;
    background-size: 26px;
    border-radius: 100%;
    transition: background-color 0.3s;

    &:hover {
      background-color: $primary;
      transition: background-color 0.3s;
    }
  }

  &__item-media-link_vk {
    background-image: url("../images/vk.svg");
  }

  &__item-media-link_instagram {
    background-image: url("../images/instagram.svg");
  }

  &__item-media-link_facebook {
    background-image: url("../images/facebook.svg");
  }

  &__item-media-link_whatsapp {
    background-image: url("../images/whatsapp.svg");
    background-size: 16px;
  }

  &__partners-baner {
    width: 85px;
    height: 61px;
    margin-bottom: 70px;
    background-image: url("../images/partner.jpg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
  }

  &__informative-links {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    padding: 21px;
  }

  &__payment-methods {
    display: flex;
  }

  &__payment-method {
    display: inline-block;
    height: 38px;
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;

    &:not(:last-child) {
      margin-right: 40px;
    }
  }

  &__payment-method_visa {
    width: 61px;
    background-image: url("../images/footer/visa.png");
  }

  &__payment-method_mastercard {
    width: 171px;
    background-image: url("../images/footer/mastercard.png");
  }

  &__payment-method_mir {
    width: 70px;
    background-image: url("../images/footer/mir.png");
  }

  &__payment-method_bank {
    width: 92px;
    background-image: url("../images/footer/bank-building.png");
  }

  &__delivery-methods {
    display: flex;
  }

  &__delivery-method {
    display: block;
    height: 38px;
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
  }

  &__delivery-method_cdek {
    width: 90px;
    margin-right: 30px;
    background-image: url("../images/footer/cdek.png");
  }

  &__delivery-method_russian-post {
    width: 80px;
    background-image: url("../images/footer/Russian-Post.png");
  }

  &__copyright {
    display: block;
    width: 100%;
    height: 50px;
    font-family: "Cera Pro", sans-serif;
    font-size: 14px;
    font-weight: 300;
    line-height: 50px;
    color: $light-gray1;
    text-align: center;
    border-top: 1px solid $light-gray3;
  }
}

@media screen and (max-width: 1200px) {
  .footer {
    &__items {
      display: grid;
      grid-template-rows: repeat(2, auto);
      grid-template-columns: repeat(3, 1fr);
      row-gap: 38px;
    }

    &__item {
      &:last-child {
        grid-area: 2/2/3/4;
      }
    }
  }
}

@media screen and (max-width: 840px) {
  .footer {
    &__informative-links {
      flex-direction: column;
      align-items: flex-start;
    }

    &__payment-methods {
      margin-bottom: 30px;
    }
  }
}

@media screen and (max-width: 630px) {
  .footer {
    &__logo {
      margin: 0 auto;
    }

    &__media-links_mobile {
      display: flex;
      justify-content: center;
      margin: 40px 0;
    }

    &__items {
      display: grid;
      grid-template-rows: repeat(3, auto);
      grid-template-columns: repeat(2, 1fr);
    }

    &__item {
      &:last-child {
        grid-area: 3/1/4/3;
      }
    }

    &__input {
      width: 208px;
      margin-right: 10px;
    }

    &__wrapper-media {
      display: none;
    }

    &__payment-method {
      height: 24px;

      &:not(:last-child) {
        margin-right: 20px;
      }
    }

    &__payment-method_visa {
      width: 39px;
    }

    &__payment-method_mastercard {
      width: 108px;
    }

    &__payment-method_mir {
      width: 51px;
    }

    &__payment-method_bank {
      width: 64px;
    }

    &__delivery-method {
      height: 21px;
    }

    &__delivery-method_cdek {
      width: 50px;
      margin-right: 12px;
    }

    &__delivery-method_russian-post {
      width: 33px;
    }

    &__copyright {
      font-size: 10px;
    }
  }
}
