.product-banner {
  &__wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 502px;
    background-color: #f36805;
  }

  &__text {
    // display: flex;
    // flex-direction: column;
    // align-items: center;
    // justify-content: center;
    max-width: 397px;
    margin-left: 85px;
  }

  &__title {
    margin-bottom: 5px;
    font-family: "Cera Pro", sans-serif;
    font-size: 52px;
    font-weight: bold;
    line-height: 130%;
    color: $white;
    text-transform: uppercase;
  }

  &__paragraph {
    margin-bottom: 70px;
    font-family: "Cera Pro", sans-serif;
    font-size: 14px;
    line-height: 150%;
    color: $white;
  }

  &__btn {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 12px 42px;
    font-family: "Cera Pro", sans-serif;
    font-size: 14px;
    font-weight: 500;
    line-height: 150%;
    color: $white;
    background-color: transparent;
    border: 1px solid $white;
    border-radius: 7px;
  }

  &__image {
    width: 35%;
    height: 100%;
    object-fit: contain;
  }
}

@media screen and (max-width: 850px) {
  .product-banner {
    &__text {
      margin-left: 30px;
    }

    &__title {
      font-size: 36px;
    }

    &__image {
      display: flex;
      flex-shrink: 0;
      width: 45%;
    }
  }
}

@media screen and (max-width: 660px) {
  .product-banner {
    &__wrapper {
      flex-direction: column-reverse;
      justify-content: center;
      height: max-content;
      padding: 50px 25px;
    }

    &__image {
      height: 230px;
    }

    &__text {
      display: flex;
      flex-direction: column;
      align-items: center;
      width: 100%;
      margin-left: 0;
    }

    &__paragraph {
      margin-bottom: 35px;
    }
  }
}

@media screen and (max-width: 550px) {
  .product-banner {
    &__image {
      height: 150px;
    }

    &__title {
      font-size: 32px;
    }

    &__paragraph {
      text-align: center;
    }
  }
}
