.popular-products {
  padding: 60px 0;

  &__title {
    margin-bottom: 45px;
  }

  // &__wrapper {
  // }

  &__items {
    display: grid;
    grid-template-rows: repeat(2, 1fr);
    grid-template-columns: repeat(2, 1fr);
    row-gap: 35px;
    column-gap: 23px;
    width: 100%;
  }

  &__item {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    height: 100%;
    overflow: hidden;
    background: #fff;
    border: 1px solid #dddee3;
    border-radius: 10px;
    box-shadow: 0 10px 20px rgba(83, 85, 108, 0.15);

    &::before {
      position: absolute;
      top: 0;
      left: 0;
      display: block;
      width: 8px;
      height: 100%;
      content: "";
    }

    &:nth-child(1) {
      &::before {
        background-color: #84bd00;
      }
    }

    &:nth-child(2) {
      &::before {
        background-color: #485cc7;
      }
    }

    &:nth-child(3) {
      &::before {
        background-color: #ff6900;
      }
    }

    &:nth-child(4) {
      &::before {
        background-color: #e40046;
      }
    }
  }

  &__item-illustration {
    display: block;
    width: 100%;
    height: auto;
    object-fit: contain;
  }

  &__footer {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    height: 75px;
    padding: 8px 41px;
  }

  &__item-title {
    margin-right: 8px;
    font-family: "Cera Pro", sans-serif;
    font-size: 20px;
    font-weight: 500;
    line-height: 130%;
    color: #53556c;
    text-transform: uppercase;
  }

  &__btn-catalog {
    padding: 6px 20px;
    font-family: "Cera Pro", sans-serif;
    font-size: 11px;
    font-weight: bold;
    line-height: 14px;
    color: $white;
    text-transform: uppercase;
    background-color: $primary;
    border-radius: 5px;
  }
}

@media screen and (max-width: 650px) {
  .popular-products {
    &__title {
      font-size: 26px;
    }

    &__items {
      display: flex;
      flex-direction: column;
      row-gap: 25px;
      column-gap: 0;
      align-items: center;
      width: 100%;
    }
  }
}
