// colors

$white: #fff;
$black: #000;
$gray: #ccc;

$primary: rgba(0, 55, 41, 0.8);
$primary-accent: #003729;
$primary-light: rgba(0, 55, 41, 0.5);

$dark-gray1: #1a1b22;
$dark-gray2: #393a4a;
$dark-gray3: #53556c;

$light-gray1: #abacb9;
$light-gray2: #bcbdc7;
$light-gray3: #dddee3;
$light-gray4: #eeeef1;
$light-gray5: #f7f7f8;

$error:  #d00000;

// styles

body {
  font-family: "Cera Pro", serif;
}

.wrapper {
  width: 94%;
  max-width: 1220px;
  margin: 0 auto;
}

.title-section {
  width: max-content;
  font-family: "Cera Pro", sans-serif;
  font-size: 36px;
  font-weight: bold;
  line-height: 130%;
  color: #434a54;
  text-transform: uppercase;
}

.page-title {
  font-family: "Cera Pro", sans-serif;
  font-size: 36px;
  font-weight: bold;
  line-height: 130%;
  color: #231f20;
  text-transform: uppercase;
}

.body-text {
  font-family: "Cera Pro", sans-serif;
  font-size: 14px;
  line-height: 150%;
  color: $dark-gray2;
}

.secondary-button {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 11px 42px;
  font-family: "Cera Pro", sans-serif;
  font-size: 14px;
  font-weight: 500;
  line-height: 150%;
  color: $dark-gray3;
  background-color: transparent;
  border: 1px solid $dark-gray3;
  border-radius: 7px;
  transition: background-color 0.3s, color 0.3s;

  &:hover {
    color: $white;
    background-color: $dark-gray3;
    transition: background-color 0.3s, color 0.3s;
  }

  &:disabled {
    pointer-events: none;
    opacity: 0.4;

    &:hover {
      color: $dark-gray3;
      background-color: transparent;
    }
  }

  &_small {
    padding: 5px 20px;
    font-size: 11px;
    color: #003729;
    text-transform: uppercase;
    border: 1px solid #003729;

    &:hover {
      color: $white;
      background-color: $primary-light;
      transition: background-color 0.3s, color 0.3s;
    }
  }
}

.primary-button {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 12px 42px;
  font-family: "Cera Pro", sans-serif;
  font-size: 14px;
  font-weight: bold;
  line-height: 150%;
  color: $white;
  background-color: $primary-accent;
  border-radius: 7px;
  transition: background-color 0.3s;

  &:hover {
    background-color: $primary;
    transition: background-color 0.3s;
  }

  &:disabled {
    opacity: 0.4;

    &:hover {
      background-color: transparent;
    }
  }

  &_small {
    height: 40px;
    padding: 7px 20px;
    font-size: 11px;
    line-height: 11px;
    color: $white;
    background-color: $primary-accent;

    &:hover {
      background-color: $primary;
      transition: background-color 0.3s;
    }
  }
}

.input {
  position: relative;
  display: block;
  width: 100%;
  font-family: "Cera Pro", sans-serif;

  &__title {
    margin-bottom: 4px;
    margin-left: 22px;
    font-family: "Cera Pro", sans-serif;
    font-size: 12px;
    line-height: 18px;
    color: $dark-gray2;
    opacity: 0;
    transition: opacity 0.3s;
  }

  &_focus &__title {
    opacity: 1;
    transition: opacity 0.3s;
  }

  &__item {
    display: flex;
    align-items: center;
    width: 100%;
    height: 40px;
    padding: 0 20px;
    padding-right: 45px;
    font-family: "Cera Pro", sans-serif;
    font-size: 14px;
    line-height: 150%;
    color: $dark-gray2;
    background-color: $white;
    border: 1px solid $light-gray1;
    border-radius: 5px;
  }

  &_valide::after {
    position: absolute;
    top: 18px;
    right: 20px;
    display: block;
    width: 20px;
    height: 40px;
    content: "";
    background-image: url("../images/valide-form.svg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
  }

  &_required::after {
    position: absolute;
    top: 18px;
    right: 20px;
    display: block;
    width: 20px;
    height: 40px;
    content: "";
    background-image: url("../images/required.svg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: 8px;
  }

  &__error {
    margin-top: 4px;
    margin-left: 22px;
    font-family: "Cera Pro", sans-serif;
    font-size: 12px;
    line-height: 18px;
    color: $error;
    opacity: 0;
    transition: opacity 0.3s;
  }

  &_error &__error {
    opacity: 1;
    transition: opacity 0.3s;
  }
}

.password {
  position: relative;
  display: block;
  width: 100%;
  font-family: "Cera Pro", sans-serif;

  &__title {
    margin-bottom: 4px;
    margin-left: 22px;
    font-family: "Cera Pro", sans-serif;
    font-size: 12px;
    line-height: 18px;
    color: $dark-gray2;
    opacity: 0;
    transition: opacity 0.3s;
  }

  &_focus &__title {
    opacity: 1;
    transition: opacity 0.3s;
  }

  &__item {
    display: flex;
    align-items: center;
    width: 100%;
    height: 40px;
    padding: 0 20px;
    padding-right: 45px;
    font-family: "Cera Pro", sans-serif;
    font-size: 14px;
    line-height: 150%;
    color: $dark-gray2;
    background-color: $white;
    border: 1px solid $light-gray1;
    border-radius: 5px;
  }

  &__toggle-show {
    position: absolute;
    top: 18px;
    right: 20px;
    display: block;
    width: 20px;
    height: 40px;
    background-color: transparent;
    background-image: url("../images/Hide.svg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: 15px;
    transition: background-image 0.4s;
  }

  &_show &__toggle-show {
    background-image: url("../images/Show.svg");
  }

  &__error {
    margin-top: 4px;
    margin-left: 22px;
    font-family: "Cera Pro", sans-serif;
    font-size: 12px;
    line-height: 18px;
    color: $error;
    opacity: 0;
    transition: opacity 0.3s;
  }

  &_error &__error {
    opacity: 1;
    transition: opacity 0.3s;
  }
}

.select {
  position: relative;
  display: block;
  width: 100%;
  height: 40px;
  margin-top: 20px;
  font-family: "Cera Pro", sans-serif;
  font-size: 14px;
  line-height: 40%;
  color: $dark-gray2;
  cursor: pointer;
  border: 1px solid $light-gray1;
  border-radius: 5px;

  &::after {
    position: absolute;
    top: 0;
    right: 20px;
    display: block;
    width: 10px;
    height: 40px;
    pointer-events: none;
    content: "";
    background-image: url("../images/arrow-down.svg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
  }

  &_open::after {
    transform: rotate3d(1, 0, 0, 180deg);
  }

  &__label {
    position: absolute;
    top: -20px;
    left: 22px;
    display: none;
    font-family: "Cera Pro", sans-serif;
    font-size: 12px;
    line-height: 18px;
    color: $dark-gray3;
  }

  &_focus &__label {
    display: inline-block;
  }

  &__item {
    width: 100%;
    height: 100%;
    padding-right: 40px;
    padding-left: 20px;
    background: #fff;
    border-radius: 5px;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
  }

  // &__option {
  // }
}

.checkbox {
  position: relative;
  display: flex;
  flex-direction: row-reverse;
  align-items: center;
  width: max-content;
  max-width: 100%;
  padding-left: 24px;
  font-family: "Cera Pro", sans-serif;
  font-size: 14px;
  line-height: 150%;
  color: $dark-gray3;
  cursor: pointer;

  &::before {
    position: absolute;
    top: 4px;
    left: 0;
    display: block;
    width: 13px;
    height: 13px;
    content: "";
    border: 1px solid $primary;
  }

  &_checked::after {
    position: absolute;
    top: 4px;
    left: 0;
    display: block;
    width: 13px;
    height: 13px;
    content: "";
    background-image: url("../images/valide-form.svg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: 9px;
  }

  &__input {
    position: absolute;
    top: 0;
    left: 0;
    width: 1px;
    height: 1px;
    opacity: 0;
  }
}

.radio-payment {
  position: relative;
  display: flex;
  flex-direction: column-reverse;
  width: 100%;
  height: max-content;
  padding: 30px;
  margin-bottom: 20px;
  font-family: "Cera Pro", sans-serif;
  font-size: 13px;
  font-weight: bold;
  line-height: 150%;
  color: $dark-gray3;
  text-transform: uppercase;
  cursor: pointer;
  background: $light-gray5;
  border: 1px solid $light-gray1;
  border-radius: 5px;

  &::after {
    position: absolute;
    top: 50px;
    right: 32px;
    width: 18px;
    height: 18px;
    content: "";
    border: 2px solid $primary;
    border-radius: 5px;
  }

  &_checked::after {
    background-image: url("../images/valide-form.svg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: 10px;
  }

  &__svg {
    display: inline-block;
    width: 24px;
    height: 24px;
    margin-bottom: 25px;
    fill: $white;
    stroke: $dark-gray3;
  }

  &__input {
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0;
  }
}

.upload-files {
  display: block;
  width: 100%;
  height: 162px;
  background-color: #fff;
  background-image: url("../images/upload-file.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: 38px;
  border: 1px solid $light-gray1;
  border-radius: 5px;

  &__input {
    width: 100%;
    height: 100%;
    padding: 20px;
    border-radius: 5px;

    &_hide {
      opacity: 0;
    }
  }
}

.primary-tabs {
  position: relative;
  display: flex;
  align-items: center;
  width: max-content;
  max-width: 100%;
  height: auto;
  min-height: 36px;
  padding: 5px 24px;
  margin-bottom: 7px;
  font-family: "Cera Pro", sans-serif;
  font-size: 14px;
  line-height: 150%;
  color: $dark-gray3;
  cursor: pointer;
  background-color: transparent;
  border: 1px solid $dark-gray3;
  border-radius: 5px;
  box-shadow: 0 2px 9px rgba(0, 0, 0, 0.15);
  transition: color 0.4s;

  &:hover {
    color: $white;
    transition: color 0.4s;
  }

  &__input {
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
    width: 100%;
    height: 100%;
    cursor: pointer;
    background-color: transparent;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    border-radius: 5px;
    transition: background-color 0.4s;

    &:checked {
      background-color: $light-gray3;
      transition: background-color 0.4s;
    }

    &:hover {
      color: $white;
      background-color: $light-gray2;
      transition: background-color 0.4s;
    }
  }
}

.add-cart {
  display: flex;
  align-items: center;
  justify-content: center;
  width: max-content;
  padding: 12px 35px;
  font-family: "Cera Pro", sans-serif;
  font-size: 12px;
  font-weight: bold;
  line-height: 150%;
  color: $white;
  text-transform: uppercase;
  background-color: $primary-accent;
  border-radius: 5px;
  transition: background-color 0.4s;

  &__svg {
    display: inline-block;
    width: 24px;
    height: 24px;
    margin-left: 10px;
    fill: transparent;
    stroke: $white;
  }

  &:hover {
    background-color: $primary;
  }

  &_disable {
    pointer-events: none;
    opacity: 0.4;
  }
}

.tabs-toggle {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 65px;
  margin-bottom: 35px;
  overflow: hidden;
  border: 1px solid $light-gray1;
  border-radius: 10px;

  &__btn {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    padding: 14px 0;
    font-family: "Cera Pro", sans-serif;
    font-size: 13px;
    font-weight: bold;
    line-height: 150%;
    color: $dark-gray3;
    text-transform: uppercase;
    background-color: transparent;
    transition: color 0.4s, background-color 0.4s;

    &_selected {
      color: $white;
      background-color: $primary;
      border-radius: 10px;
    }
  }
}

input[type="number"]::-webkit-outer-spin-button,
input[type="number"]::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type='number'] {
  -moz-appearance: textfield;
}
