.about {
  margin-bottom: 40px;

  &__wrapper {
    display: block;
    padding: 60px 73px;
    padding-bottom: 95px;
    background: $light-gray5;
    border-radius: 15px;
    box-shadow: 0 10px 20px rgba(83, 85, 108, 0.15);
  }

  &__title {
    margin: 0 auto;
    margin-bottom: 70px;
    color: #231f20;
  }

  &__items {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &__item {
    max-width: 284px;
    padding-top: 121px;
    text-align: center;
    background-repeat: no-repeat;
    background-position: center top;
    background-size: 86px;

    &:nth-child(1) {
      background-image: url("../images/about/star.svg");
    }

    &:nth-child(2) {
      background-image: url("../images/about/shield.svg");
    }

    &:nth-child(3) {
      background-image: url("../images/about/chart.svg");
    }
  }

  // &__text {
  // }
}

@media screen and (max-width: 1111px) {
  .about {
    &__wrapper {
      padding: 45px 30px;
      padding-bottom: 75px;
    }

    &__item:not(:last-child) {
      margin-right: 20px;
    }
  }
}

@media screen and (max-width: 850px) {
  .about {
    &__wrapper {
      padding: 30px 20px;
      padding-bottom: 75px;
    }

    &__title {
      margin-bottom: 0;
    }

    &__items {
      flex-wrap: wrap;
      justify-content: space-evenly;
      padding: 45px 30px;
      padding-bottom: 75px;
    }

    &__item {
      &:not(:last-child) {
        margin-right: 20px;
      }

      flex-shrink: 0;
      min-width: 200px;
      margin: 20px;
    }
  }
}
