.media {
  padding: 40px 0;

  &__wrapper {
    display: flex;
    justify-content: space-between;
  }

  &__title {
    position: relative;
    display: inline-block;
    max-width: 275px;
    height: max-content;
    padding-left: 43px;
    margin-bottom: 25px;

    &::before {
      position: absolute;
      top: -10%;
      left: 0;
      display: block;
      width: 8px;
      height: 110%;
      content: "";
      background-color: #ff6900;
    }
  }

  &__items {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 55px;
    width: 100%;
    max-width: 802px;
  }

  &__item {
    width: 100%;
    overflow: hidden;
  }

  &__illustration {
    width: 100%;
    height: auto;
    object-fit: contain;
    margin-bottom: 30px;
    overflow: hidden;
    border: 1px solid $light-gray2;
    border-radius: 10px;
  }

  &__item-title {
    margin-bottom: 14px;
    font-family: "Cera Pro", sans-serif;
    font-size: 16px;
    font-weight: bold;
    line-height: 130%;
    color: #393a4a;
    text-transform: uppercase;
  }

  &__text {
    height: 63px;
    margin-bottom: 14px;
    overflow: hidden;
  }

  &__more-detailed {
    position: relative;
    display: block;
    width: max-content;
    font-family: "Cera Pro", sans-serif;
    font-size: 14px;
    line-height: 18px;
    color: #595377;
    text-decoration: underline;
  }

  &__more {
    margin: 50px auto;
    margin-bottom: 0;
  }
}

@media screen and (max-width: 1200px) {
  .media {
    &__wrapper {
      flex-direction: column;
    }

    &__title {
      position: relative;
      display: inline-block;
      width: max-content;
      max-width: 100%;
      height: max-content;
      padding-left: 43px;
    }

    &__items {
      margin-left: auto;
    }
  }
}

@media screen and (max-width: 650px) {
  .media {
    &__title {
      margin-bottom: 45px;
      font-size: 26px;
    }

    &__items {
      display: flex;
      flex-direction: column;
      gap: 0;
      align-items: center;
    }

    &__item {
      margin-bottom: 45px;
    }

    &__more {
      margin: 25px auto;
    }
  }
}
