.bonuses {
  padding: 67px;

  &__wrapper {
    padding: 40px 0;
    background-image: url("../images/bonusesBG.png");
    background-repeat: no-repeat;
    background-position: right center;
    background-size: auto 100%;
  }

  &__text-block {
    display: block;
    width: 58%;
    max-width: 708px;
    padding: 60px;
    padding-top: 48px;
    background: $white;
    border: 1px solid #e3e3e3;
    border-radius: 10px;
    box-shadow: 0 4px 30px rgba(89, 83, 119, 0.25);
  }

  &__title {
    margin-bottom: 24px;
    font-family: "Cera Pro", sans-serif;
    font-size: 20px;
    font-weight: bold;
    line-height: 130%;
    color: #434a54;
    text-transform: uppercase;
  }

  &__paragraph {
    margin-bottom: 34px;
  }

  // &__btn {
  // }
}

@media screen and (max-width: 1300px) {
  .bonuses {
    padding: 65px 0;
  }
}

@media screen and (max-width: 1180px) {
  .bonuses {
    padding: 45px 0;

    &__wrapper {
      padding: 0;
      background-image: none;
    }

    &__text-block {
      display: block;
      width: 100%;
      max-width: 100%;
      padding: 60px;
      padding-top: 48px;
      background: $white;
      border: 1px solid #e3e3e3;
      border-radius: 10px;
      box-shadow: 0 4px 30px rgba(89, 83, 119, 0.25);
    }
  }
}
