.search {
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  width: max-content;
  height: 100%;

  &__input {
    position: absolute;
    top: 0;
    right: 0;
    width: 0;
    height: 0;

    &_open {
      width: 250px;
      height: 28px;
      padding: 5px 10px;
      padding-right: 30px;
      border: 1px solid $light-gray3;
      border-radius: 5px;
    }
  }

  &__btn {
    position: relative;
    z-index: 10;
    display: block;
    width: 32px;
    height: 24px;
    background-color: transparent;
    background-image: url("../images/search.svg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: 24px;
  }
}
