.any-questions {
  padding: 67px 0;

  &__wrapper {
    padding: 80px;
    background: #f7f7f8;
    border: 1px solid #dee7ed;
    border-radius: 15px;
  }

  &__title {
    margin-bottom: 14px;
    font-family: "Cera Pro", sans-serif;
    font-size: 26px;
    font-weight: 500;
    line-height: 130%;
    color: #434a54;
    text-transform: uppercase;
  }

  &__paragraph {
    margin-bottom: 33px;
  }

  &__form {
    // display: flex;
    // flex-wrap: wrap;
    // align-items: center;
    // width: 100%;

    display: grid;
    grid-template-columns: repeat(3, 1fr);
    column-gap: 12px;
    width: 100%;
  }

  &__input {
    width: 100%;
    // max-width: 346px;
    // margin-right: 12px;
  }

  &__btn {
    display: flex;
    width: 100%;
    max-width: 346px;
    height: max-content;
    padding: 12px 24px;
    margin-top: 16px;
  }
}

@media screen and (max-width: 1000px) {
  .any-questions {
    padding: 40px 0;

    &__wrapper {
      padding: 40px;
    }

    &__form {
      display: flex;
      flex-wrap: wrap;
      column-gap: 0;
      align-items: center;
      width: 100%;
    }

    &__input {
      max-width: 346px;
      margin-right: 12px;
    }
  }
}

@media screen and (max-width: 850px) {
  .any-questions {
    &__wrapper {
      display: flex;
      flex-direction: column;
      align-items: center;
    }

    &__paragraph {
      text-align: center;
    }

    &__form {
      flex-direction: column;
      align-items: center;
    }

    &__input {
      margin-right: 0;
    }

    &__btn {
      margin-top: 15px;
    }
  }
}
